body {
  margin: 45px 10% 0;
  text-align: center;
  overflow-x: hidden;
  font-family: 'Montserrat', sans-serif;
}



#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}


.main-content {
  flex: 1;
}


@media (max-width: 768px) {
  body {
    margin: 45px 5% 0;
  }
}